.main-title {
  background-image: url(./my_components/common/image/starts.jpg);
  background-color: rgba(255, 255, 255, 0.2); /* 배경색을 반투명하게 설정 */
  background-blend-mode: lighten;
}

.title-box > .over-text {
  overflow: hidden;
  display: inline-block;
  white-space: nowrap;

  cursor: pointer;
}

.title-box:hover > .over-text {
  animation: scrollText 7s linear infinite;
}

.not-over-text {
  cursor: pointer;
}

@keyframes scrollText {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(calc(-100%));
  }
}

.view-box::-webkit-scrollbar, .left-view-box::-webkit-scrollbar {
    /* 스크롤바 전체 */
    width: 10px;
    border-radius: 5px;
}

.view-box::-webkit-scrollbar-thumb, .left-view-box::-webkit-scrollbar-thumb {
    /* 현재 위치 스크롤바 */
    background-color: rgb(154, 255, 53, 0.5);
    border-radius: 5px;

    /* 막대 padding 설정 */
    background-clip: padding-box;
    border: 2.5px solid transparent;
}

.view-box::-webkit-scrollbar-track, .left-view-box::-webkit-scrollbar-track {
    /* 외부 스크롤바 */
    background-color: #dddddd;
    border-radius: 5px;
}